.GraphPDF {
  box-sizing: content-box;
}
.GraphPDF .AnnotatedGraphMain {
  background: #F6F4EF;
}
.GraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader {
  padding-top: 16px;
  height: 180px;
  padding-left: 30px;
  padding-right: 30px;
}
.GraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphTitle {
  margin-bottom: -30px;
  font-family: 'Source Serif VF', Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-weight: 700;
  font-size: 48px;
  width: 60%;
}
.GraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphPartition {
  font-size: 12px;
  margin-bottom: 2px;
  float: right;
}
.GraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphCalliope {
  font-size: 12px;
  margin-top: 45px;
  float: right;
  font-family: '-apple-system, BlinkMacSystemFont, ' Segoe UI ', Roboto, ' Helvetica Neue ', Arial, ' Noto Sans ', sans-serif, ' Apple Color Emoji ', ' Segoe UI Emoji ', ' Segoe UI Symbol ', ' Noto Color Emoji '';
}
.GraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphCalliope .AnnotatedGraphCalliopeIcon {
  display: inline-block;
  width: 16px;
  height: 12px;
  background: url(/static/media/calliope.a028ba8c.svg) no-repeat center;
  background-size: contain;
  margin-right: 5px;
  margin-bottom: -1px;
}
.GraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphDivider {
  margin-top: 0px;
  margin-bottom: 8px;
  border-top: 1px solid #3E3A39;
}
.GraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphDescription {
  width: 100%;
  height: 90px;
  line-height: 1.4em;
  font-family: '-apple-system, BlinkMacSystemFont, ' Segoe UI ', Roboto, ' Helvetica Neue ', Arial, ' Noto Sans ', sans-serif, ' Apple Color Emoji ', ' Segoe UI Emoji ', ' Segoe UI Symbol ', ' Noto Color Emoji '';
  text-align: justify;
}
.GraphPDF .AnnotatedGraphSummarization {
  background: #F2EFE6;
}
.verticalGraphPDF {
  width: 825px;
  height: 1166px;
}
.verticalGraphPDF .AnnotatedGraphMain {
  height: 860px;
}
.verticalGraphPDF .AnnotatedGraphMain .AnnotatedGraphContent {
  width: 825px;
  height: 680px;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}
.verticalGraphPDF .AnnotatedGraphSummarization {
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 7px;
  height: 306px;
}
.horizontalGraphPDF {
  width: 1166px;
  height: 825px;
  display: flex;
}
.horizontalGraphPDF .AnnotatedGraphMain {
  width: 854px;
  height: 100%;
}
.horizontalGraphPDF .AnnotatedGraphMain .AnnotatedGraphContent {
  width: 825px;
  -webkit-transform: translateY(-18px);
          transform: translateY(-18px);
}
.horizontalGraphPDF .AnnotatedGraphSummarization {
  width: 312px;
  height: 100%;
}
.printGraphPDF {
  border: 3px solid #dedede;
}
.printGraphPDF .AnnotatedGraphMain {
  background: #FFFFFF;
}
.printGraphPDF .AnnotatedGraphSummarization {
  background: #F7F8F8;
}
.printGraphPDF .AnnotatedGraphSummarization .summarizationCard .summarizationCardFact {
  background: #DCDDDD;
}
.printGraphPDF .AnnotatedGraphSummarization .summarizationCard .summarizationCardVis {
  background: #EFEFEF;
}
.printGraphPDF .AnnotatedGraphSummarization .summarizationCard .summarizationCardVis .bar {
  fill: #9FA0AA;
}
.darkGraphPDF {
  color: #FFFFFF;
}
.darkGraphPDF .AnnotatedGraphMain {
  background: #0E1939;
}
.darkGraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphTitle {
  font-family: 'PangMenZhengDaoBiaoTiTi';
}
.darkGraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphDivider {
  border-top: 1px solid #FFFFFF;
}
.darkGraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .legends .iconFill {
  fill: #FFFFFF;
}
.darkGraphPDF .AnnotatedGraphMain .factCard .factCardContent {
  box-shadow: 8px 8px 8px rgba(35, 24, 21, 0.3);
}
.darkGraphPDF .AnnotatedGraphMain .coloredText {
  fill: #fff;
}
.darkGraphPDF .AnnotatedGraphSummarization {
  background: #222C46;
}
.darkGraphPDF .AnnotatedGraphSummarization .graphSummarization .graphSummarizationHeader .graphSummarizationDividerContainer .graphSummarizationDivider {
  border-color: #FFFFFF;
}
.darkGraphPDF .AnnotatedGraphSummarization .summarizationCard .summarizationCardFact {
  background: #0A142F;
  box-shadow: 7px 7px 7px rgba(35, 24, 21, 0.3);
}
.darkGraphPDF .AnnotatedGraphSummarization .summarizationCard .summarizationCardVis {
  background: #162245;
}
.darkGraphPDF .AnnotatedGraphSummarization .summarizationCard .summarizationCardVis .bar {
  fill: #0A142F;
}
.communityIcon {
  display: inline-block;
  width: 12px;
  height: 12px;
}
.highlightText {
  font-weight: 600;
}
.GraphPDFStatic .deleteFact {
  display: none;
}
