
/* 生成pdf的外部css引入及修改 */ /* 注意pdfkit 只用较老版本的样式 */
/* 一些不同如下 */
/*  display: flex; --->  display:-webkit-flex; display: -webkit-box*/
/*  align-items: center----> align-items: -webkit-center */
/* 不支持 calc()  */
/* -webkit-box-pack: center; 元素水平居中 */
/* -webkit-box-align: center;  元素垂直居中 */
/* trasform -> -webkit-transform */

.ant-layout, .ant-layout * {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
.ant-row {
    display: -ms-flexbox;
    display: flex;
    display:-webkit-flex;
     display: -webkit-box;
    -ms-flex-flow: row wrap;
    flex-flow: row wrap;
    -webkit-flex-flow: row wrap;
}
.ant-col {
    position: relative;
    max-width: 100%;
    min-height: 1px;
}

.ant-col-3 {
    display: block;
    -ms-flex: 0 0 12.5%;
    flex: 0 0 12.5%;
    max-width: 12.5%;
}

.ant-col-9 {
    display: block;
    -ms-flex: 0 0 37.5%;
    flex: 0 0 37.5%;
    -webkit-flex: 0 0 37.5%;
    max-width: 37.5%;
}
.ant-col-15 {
    display: block;
    -ms-flex: 0 0 62.5%;
    flex: 0 0 62.5%;
    -webkit-flex: 0 0 62.5%;
    max-width: 62.5%;
}

.ant-col-20 {
    display: block;
    -ms-flex: 0 0 83.33333333%;
    flex: 0 0 83.33333333%;
    max-width: 83.33333333%;
}

.ant-col-offset-1 {
    margin-left: 4.16666667%;
}

.ant-divider {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: rgba(0, 0, 0, 0.85);
    font-size: 14px;
    font-variant: tabular-nums;
    line-height: 1.5715;
    list-style: none;
    -webkit-font-feature-settings: 'tnum', "tnum";
    font-feature-settings: 'tnum', "tnum";
    border-top: 1px solid rgba(0, 0, 0, 0.06);
}

.ant-divider-horizontal {
    display: -ms-flexbox;
    display: flex;
    display:-webkit-flex; 
    display: -webkit-box;
    clear: both;
    width: 100%;
    min-width: 100%;
    margin: 24px 0;
}

.ant-divider-dashed {
    background: none;
    border-color: rgba(0, 0, 0, 0.06);
    border-style: dashed;
    border-width: 1px 0 0;
}

body{
    background-color: rgb(249, 248, 246);
    background: rgb(249, 248, 246)
}

*{
    margin: 0;
    -webkit-box-sizing: content-box;
}

.GraphPDF{
    box-sizing: content-box;
}

.AnnotatedGraphMain {
    background: #F6F4EF;
}

.AnnotatedGraphHeader{
    padding-top: 16px;
    height: 180px;
    padding-left: 30px;
    padding-right: 30px;             
}

.AnnotatedGraphTitle{
    margin-bottom: -30px;
    font-family: 'Source Serif VF',
    Arial,
    "Lucida Family",
    Verdana,
    Tahoma,
    "Trebuchet MS";
    font-weight: 700; 
    font-size: 48px;
    width: 60%;
}

.AnnotatedGraphPartition{
    font-size: 12px;
    margin-bottom: 2px;
    text-align: right;
}

