.AnnotatedGraphDivider{
    margin-top: 0px;
    margin-bottom: 8px;
    border-top: 1px solid #3E3A39;
}

.AnnotatedGraphDescription{
    width: 100%;
    height: 90px;
    line-height: 1.4em;
    font-family: '-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji'';
    text-align: justify;
}

.AnnotatedGraphSummarization{
    background: #F2EFE6;
}

.annotatedGraphVis{
    position: relative;
    top: -22px;
    width: fit-content;
    margin: 15px 62.5px;

    /* height: 600px;
    overflow: hidden; */
}

.annotatedGraphVis #annotatedGraphVisContainer{
    width: fit-content;
}

.maskHighlight{
    position: fixed;
    z-index: 999;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.factCard{
    position:absolute;
}
    
.factCard .factCardIndex{
    width: 35px;
    color: #fff;
    text-align: center;
    font-family: '-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji'';
    font-size: 13px;
    padding-top: 2px; 
    line-height: 18px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.factCard .factCardContent{
    /* fill-opacity: 0.2; */
    /* filter: alpha(opacity=50); */
    /* -khtml-opacity: 0.9; */
    border-radius: 8px;
    border-top-left-radius: 0px;
    box-shadow: 8px 8px 8px rgba(175,164,161,0.3); 
    padding: 10px;
    display: flex;
    display: -webkit-flex; 
    display: -webkit-box;
    align-items: stretch;
}

.factCard .factCardContentDetail{
    min-width: 20px;
    border-right: 1px dashed; 
    padding-right: 8px;
    margin-right: 8px;
    position: relative;
}

.factCard .factCardDescription{
    width: 160px;
    line-height: 1.6em;
    font-size: 14px; 
}
.factCard .factCardDescription *{
    overflow-wrap: normal;
}

.factCard .factCardVis{
    width: min-content;
    margin: 0 auto;
}

.factCard .factCardVisrank{
    margin: 0;
}
            
.factCard .factCardContentDetail:last-child{
    border-right: 0px; 
    padding-right: 0px;
    margin-right: 0px;
}

.factCard .deleteFact {
    right: -10px;
}



.legends{
    font-size: 12px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
}

.legend{
    display        : inline-block;
    width          : 13px;
    height         : 16px;
    margin-right   : 10px;
}

.legendExtreme{
    width          : 14px;
}

.legendOutlier{
    width          : 15px;
    margin-right   : 9px;
}

.legendProportion{
    margin-right   : 11px;
}

.legendRank{
    margin-right   : 11px;
}

.legendText{
    font-size      : 14px;
    vertical-align: text-bottom;
    display: inline-block;
    position: relative;
    top: -3px;
}

.graphSummarization .graphSummarizationHeader{
    display: flex;
    display: -webkit-flex; 
    display: -webkit-box;
    height: '45px';
}
.graphSummarization .graphSummarizationTitle{
    margin: 24px 0;
    line-height: 0px;
    font-size: 18px; 
    font-weight: 600;
    width: 180px;
}
.graphSummarization .graphSummarizationDividerContainer{
    display: inline-block;
    height: 30px;
}
.graphSummarization .graphSummarizationDivider{
    border-color: #3E3A39;
}

.summarizationCard{
    display: inline-block;
    /* 不可以删，删了会导致card高低不平 */
    float: left;
}

.summarizationCard .summarizationCardFact{
    display: inline-block;
    background: #D7D4CD;
    border-radius: 5px;
    box-shadow: 7px 7px 7px #bbb;
    position: relative;
}

.summarizationCard .summarizationCardVis{
    width: 150px;
    height: 150px;
    background: #F2EFE6;
    border-radius: 6px;
    cursor: pointer;
}
.summarizationCard .summarizationCardTitle{
    text-align: center;
    padding-top: 6px; 
    font-family: '-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji'';
    font-weight: 600;
}

.summarizationCard .summarizationCardDescription{
    overflow: scroll;
    font-family: '-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji'';
    font-size: 14px; 
    line-height: 1.4em;
    overflow: hidden;
}

/* layout vertical */
.horizontalGraphSummarization .graphSummarizationHeader .graphSummarizationDividerContainer{
    width: 580px;
    margin-left: 10px;
}
.horizontalGraphSummarization .summarizationCard{
    margin-right: 35px;
}
.horizontalGraphSummarization .summarizationCard .summarizationCardFact{
    height: 225px;
    width: 165px;
}
.horizontalGraphSummarization .summarizationCard .summarizationCardFact .summarizationCardContent .summarizationCardVis{
    margin: 7px auto;
}

.horizontalGraphSummarization .summarizationCard .summarizationCardFact .summarizationCardContent .summarizationCardDescription{
    width: 148px;
    height: 55px;
    margin: 5px auto;
}
.horizontalGraphSummarization .summarizationCard:last-child{
    margin-right: 0px;
}
    
/* layout horizontal */
.verticalGraphSummarization{
    padding: 20px;
}
.verticalGraphSummarization .graphSummarizationHeader{
    flex-direction: row-reverse;
    -webkit-box-direction: reverse;
}
.verticalGraphSummarization .graphSummarizationHeader .graphSummarizationDividerContainer{
    width: 100px;
    margin-right: 10px;
}
.verticalGraphSummarization .graphSummarizationContainer{
    padding: 0;
}

.verticalGraphSummarization .summarizationCard{
    margin-top: 10px; 
    margin-bottom: 10px;
}
.verticalGraphSummarization .summarizationCard .summarizationCardContent{
    display: flex;
    display: -webkit-flex; 
    display: -webkit-box;
}
.verticalGraphSummarization .summarizationCard .summarizationCardContent .summarizationCardVis{
    margin: 7px;
    margin-right: 0;
}
.verticalGraphSummarization .summarizationCard .summarizationCardContent .summarizationCardDescription{
    width: 105px;
    height: 150px;
    margin: 7px 5px;
}

/* layout and  style */
.verticalGraphPDF {
    width: 825px;
    height: 1166px;
}
.verticalGraphPDF .AnnotatedGraphMain {
    height: 860px;
}
.verticalGraphPDF .AnnotatedGraphMain .AnnotatedGraphContent {
    width: 825px;
    height: 680px;
    transform: translateY(10px);
    -webkit-transform: translateY(10px);
}

.verticalGraphPDF .AnnotatedGraphSummarization {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 7px;
    height: 302px;
}


.horizontalGraphPDF {
    width: 1166px;
    height: 825px;
    /* overflow: hidden; */
    display: flex;
    display: -webkit-flex;
    display: -webkit-box;
}
.horizontalGraphPDF .AnnotatedGraphMain {
    width: 854px;
    height: 825px;
}
.horizontalGraphPDF .AnnotatedGraphMain .AnnotatedGraphContent {
    width: 825px;
    transform: translateY(-38px);
    -webkit-transform: translateY(-38px);
}
.horizontalGraphPDF .AnnotatedGraphSummarization {
    width: 312px;
    height: 825px;
}

.printGraphPDF {
    border: 3px solid #dedede;
}
.printGraphPDF .AnnotatedGraphMain {
    background: #FFFFFF;
}
.printGraphPDF .AnnotatedGraphSummarization {
    background: #F7F8F8;
}
.printGraphPDF .AnnotatedGraphSummarization .summarizationCard .summarizationCardFact {
    background: #DCDDDD;
}
.printGraphPDF .AnnotatedGraphSummarization .summarizationCard .summarizationCardVis {
    background: #EFEFEF;
}
.printGraphPDF .AnnotatedGraphSummarization .summarizationCard .summarizationCardVis .bar {
    fill: #9FA0AA;
}

.darkGraphPDF {
    color: #FFFFFF;
}
.darkGraphPDF .AnnotatedGraphMain {
    background: #0E1939;
}
.darkGraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphTitle{
    font-family: 'PangMenZhengDaoBiaoTiTi';
}
.darkGraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .AnnotatedGraphDivider {
    border-top: 1px solid #FFFFFF;
}
.darkGraphPDF .AnnotatedGraphMain .AnnotatedGraphHeader .legends .iconFill{
    fill: #FFFFFF;
}
            
.darkGraphPDF .AnnotatedGraphMain .factCard .factCardContent{
    box-shadow: 8px 8px 8px rgba(35,24,21,0.3);
}
.darkGraphPDF .AnnotatedGraphMain .coloredText{
    fill: #fff;
}

.darkGraphPDF .AnnotatedGraphSummarization {
    background: #222C46;
}
.darkGraphPDF .graphSummarization .graphSummarizationDivider {
    border-color: #FFFFFF;
}
.darkGraphPDF .graphSummarization .summarizationCard .summarizationCardFact {
    background: #0A142F;
    box-shadow: 7px 7px 7px rgba(35,24,21,0.3);
}
.darkGraphPDF .graphSummarization .summarizationCard .summarizationCardVis{
    background: #162245;
}
.darkGraphPDF .graphSummarization .summarizationCard .summarizationCardVis .bar {
    fill: #0A142F;
}

.communityIcon{
    display: inline-block;
    width: 12px;
    height: 12px;
}

.highlightText{
    font-weight: 600;
}