.factCard {
  position: absolute;
  top: 0;
  opacity: 0;
  -webkit-transform: translate(0px, 0px) !important;
          transform: translate(0px, 0px) !important;
}
.factCard .factCardIndex {
  width: 35px;
  color: #fff;
  text-align: center;
  font-family: '-apple-system, BlinkMacSystemFont, ' Segoe UI ', Roboto, ' Helvetica Neue ', Arial, ' Noto Sans ', sans-serif, ' Apple Color Emoji ', ' Segoe UI Emoji ', ' Segoe UI Symbol ', ' Noto Color Emoji '';
  font-size: 13px;
  padding-top: 2px;
  line-height: 18px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}
.factCard .factCardContent {
  border-radius: 8px;
  border-top-left-radius: 0px;
  box-shadow: 8px 8px 8px rgba(175, 164, 161, 0.3);
  padding: 10px;
  display: flex;
  align-items: stretch;
}
.factCard .factCardContent .factCardContentDetail {
  min-width: 20px;
  border-right: 1px dashed;
  padding-right: 8px;
  margin-right: 8px;
  position: relative;
}
.factCard .factCardContent .factCardContentDetail .factCardDescription {
  width: 160px;
  line-height: 1.6em;
  font-size: 14px;
}
.factCard .factCardContent .factCardContentDetail .factCardDescription * {
  overflow-wrap: normal;
}
.factCard .factCardContent .factCardContentDetail .factCardVis {
  width: -webkit-min-content;
  width: min-content;
  margin: 0 auto;
}
.factCard .factCardContent .factCardContentDetail .factCardVisrank {
  margin: 0;
}
.factCard .factCardContent .factCardContentDetail .deleteFact {
  position: absolute;
  top: -10px;
  right: 0px;
  width: 20px;
  height: 20px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 8px 8px;
  cursor: pointer;
}
.factCard .factCardContent .factCardContentDetail .deleteFact:hover {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 8px 8px;
}
.factCard .factCardContent .factCardContentDetail:last-child {
  border-right: 0px;
  padding-right: 0px;
  margin-right: 0px;
}
.factCard .factCardContent .factCardContentDetail:last-child .deleteFact {
  right: -10px;
}
.react-draggable-dragging * {
  cursor: move !important;
}
