.graphSummarization .graphSummarizationHeader {
  display: flex;
  height: 45px;
}
.graphSummarization .graphSummarizationHeader .graphSummarizationTitle {
  margin: 24px 0;
  line-height: 0px;
  font-size: 18px;
  font-weight: 600;
  flex-grow: 0;
}
.graphSummarization .graphSummarizationHeader .graphSummarizationDividerContainer {
  flex-grow: 1;
  display: inline-block;
  height: 30px;
}
.graphSummarization .graphSummarizationHeader .graphSummarizationDividerContainer .graphSummarizationDivider {
  border-color: #3E3A39;
}
.horizontalGraphSummarization .graphSummarizationHeader .graphSummarizationDividerContainer {
  margin-left: 10px;
}
.verticalGraphSummarization {
  padding: 20px;
}
.verticalGraphSummarization .graphSummarizationHeader {
  flex-direction: row-reverse;
}
.verticalGraphSummarization .graphSummarizationHeader .graphSummarizationDividerContainer {
  margin-right: 10px;
}
.verticalGraphSummarization .graphSummarizationContainer {
  padding: 0;
}
