@font-face {
  font-family: 'Source Serif VF';
  font-weight: 200 900;
  font-style: normal;
  font-stretch: normal;
  src: url(/static/media/SourceSerif4Variable-Roman.ttf.378b83a8.woff2) format('woff2'), url(/static/media/SourceSerif4Variable-Roman.ttf.df9bcc07.woff) format('woff'), url(/static/media/SourceSerif4Variable-Roman.75b4e1ae.ttf) format('truetype');
}
@font-face {
  font-family: 'PangMenZhengDaoBiaoTiTi';
  font-weight: 200 900;
  font-style: normal;
  font-stretch: normal;
  src: url(/static/media/PangMenZhengDaoBiaoTiTi-1.e6033c0b.ttf) format('truetype');
}
.summarizationCard {
  display: inline-block;
}
.summarizationCard .summarizationCardFact {
  display: inline-block;
  background: #D7D4CD;
  border-radius: 5px;
  box-shadow: 7px 7px 7px #bbb;
  position: relative;
}
.summarizationCard .summarizationCardVis {
  width: 150px;
  height: 150px;
  background: #F2EFE6;
  border-radius: 6px;
  cursor: pointer;
}
.summarizationCard .summarizationCardVis .summarizationCardTitle {
  text-align: center;
  padding-top: 6px;
  font-family: '-apple-system, BlinkMacSystemFont, ' Segoe UI ', Roboto, ' Helvetica Neue ', Arial, ' Noto Sans ', sans-serif, ' Apple Color Emoji ', ' Segoe UI Emoji ', ' Segoe UI Symbol ', ' Noto Color Emoji '';
  font-weight: 600;
}
.summarizationCard .summarizationCardDescription {
  overflow: scroll;
  font-family: '-apple-system, BlinkMacSystemFont, ' Segoe UI ', Roboto, ' Helvetica Neue ', Arial, ' Noto Sans ', sans-serif, ' Apple Color Emoji ', ' Segoe UI Emoji ', ' Segoe UI Symbol ', ' Noto Color Emoji '';
  font-size: 14px;
  line-height: 1.4em;
}
.summarizationCard .summarizationCardEmpty {
  border-radius: 5px;
  border: 2px dashed #D7D4CD;
  background: url(/static/media/add_gery.8edd811f.svg) no-repeat center;
  background-size: 30px;
  cursor: pointer;
}
.summarizationCard .summarizationCardEmpty:hover {
  border-color: #9FA0A0;
  background: url(/static/media/add_darker.92cf3adb.svg) no-repeat center;
  background-size: 30px;
  box-shadow: 8px 8px 8px #bbb;
}
.summarizationCard .deleteFact {
  position: absolute;
  width: 20px;
  height: 20px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 10px 10px;
  cursor: pointer;
}
.summarizationCard .deleteFact:hover {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 10px 10px;
}
.horizontalGraphSummarization .summarizationCard {
  margin-right: 35px;
}
.horizontalGraphSummarization .summarizationCard .summarizationCardFact {
  height: 225px;
  width: 165px;
}
.horizontalGraphSummarization .summarizationCard .summarizationCardFact .summarizationCardContent .summarizationCardVis {
  margin: 7px auto;
}
.horizontalGraphSummarization .summarizationCard .summarizationCardFact .summarizationCardContent .summarizationCardDescription {
  width: 148px;
  height: 55px;
  margin: 5px auto;
}
.horizontalGraphSummarization .summarizationCard:last-child {
  margin-right: 0px;
}
.horizontalGraphSummarization .summarizationCardEmpty {
  height: 223px;
  width: 165px;
}
.horizontalGraphSummarization .deleteFact {
  top: 10px;
  right: 10px;
}
.verticalGraphSummarization .summarizationCard {
  margin-top: 10px;
  margin-bottom: 10px;
}
.verticalGraphSummarization .summarizationCard .summarizationCardContent {
  display: flex;
}
.verticalGraphSummarization .summarizationCard .summarizationCardContent .summarizationCardVis {
  margin: 7px;
  margin-right: 0;
}
.verticalGraphSummarization .summarizationCard .summarizationCardContent .summarizationCardDescription {
  width: 105px;
  height: 150px;
  margin: 7px 5px;
}
.verticalGraphSummarization .summarizationCardEmpty {
  height: 160px;
  width: 270px;
}
.verticalGraphSummarization .deleteFact {
  top: 0px;
  right: 0px;
}
