/*********配置****/
.config-panel .fact-chart-preview {
  width: 500px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  background: #FFFFFF;
  box-shadow: 0px 10px 10px 0 rgba(138, 138, 138, 0.5);
}
.config-panel .fact-chart-preview svg {
  -webkit-transform: translate(40px) scale(2.8);
          transform: translate(40px) scale(2.8);
  -webkit-transform-origin: left;
          transform-origin: left;
}
.config-panel .config-control {
  padding: 15px;
}
.config-panel .config-control .caption::-webkit-scrollbar {
  display: hidden;
}
.config-panel .config-control .caption {
  font-size: 12px;
  color: #484F5D;
  line-height: 20px;
  height: 150px;
  background: #FFFFFF;
  border: 1px solid #D7D4CD;
  border-radius: 8px;
  padding: 6px;
  text-align: center;
}
.config-panel .config-control .caption .caption-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  font-size: 12px;
  line-height: 20px;
}
.config-panel .config-control .caption .caption-text div {
  font-size: 16px;
}
.config-panel .config-control .config-control-detail {
  margin-top: 15px;
  max-height: 262px;
  margin-bottom: 40px;
  overflow: scroll;
}
.config-panel .config-control .config-control-detail .ant-row {
  margin-bottom: 4px;
  border: 1px solid #D7D4CD;
  border-radius: 8px;
  height: 40px;
}
.config-panel .config-control .config-control-detail .ant-row .ant-row {
  border: 0px;
}
.config-panel .config-control .config-control-detail .ant-select {
  border: none !important;
  width: 100%;
}
.config-panel .config-control .config-control-detail .ant-select-selector {
  font-size: 16px;
  height: 38px;
  border: 0px;
  background: transparent;
  color: #777;
}
.config-panel .config-control .config-control-detail .ant-select-selector .ant-select-selection-item {
  line-height: 38px;
}
.config-panel .config-control .config-control-detail .ant-select-arrow {
  color: #D7D4CD;
}
.config-panel .config-control .config-control-detail .ant-select-arrow svg {
  width: 10px;
  height: 10px;
}
.config-panel .config-control .config-control-detail .ant-select-selection {
  border: none !important;
}
.config-panel .config-control .config-control-detail .select-box {
  width: 100%;
}
.config-panel .config-control .config-control-detail .ant-col,
.config-panel .config-control .config-control-detail .ant-select-selection-selected-value {
  font-size: 16px;
  color: #777;
  line-height: 38px;
}
.config-panel .config-control .config-control-detail .ant-select-disabled .ant-select-selector {
  height: 29px;
  border-radius: 0px 8px 8px 0px;
}
.config-panel .config-control .config-control-detail .ant-col-8 {
  height: 40px;
  margin-top: -1px;
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #D7D4CD;
  border-radius: 8px 1px 1px 8px;
  color: #444;
  font-size: 16px;
}
.config-panel .config-control .config-control-detail .channelSlot button {
  font-size: 16px;
  position: relative;
  right: 1px;
  bottom: 3px;
  color: #D7D4CD;
}
.config-panel .config-control .config-control-detail .channelSlot button svg {
  width: 10px;
  height: 10px;
}
.config-panel .config-control .config-control-detail .channelSlot button:hover {
  color: #FFBE31;
}
.config-panel .config-control .config-control-detail #add-subspace {
  color: #D7D4CD;
  border: none;
  background: transparent;
  font-size: 16px;
  line-height: 0px;
  width: 100%;
}
.config-panel .config-control .config-control-detail #add-subspace:hover {
  color: #FFBE31;
}
.config-panel .config-control .channelValue {
  padding-left: 10px;
}
.config-panel-vertical {
  width: 96vw;
}
.config-panel-vertical .fact-chart-preview {
  display: block;
  margin-left: 2vw;
  margin-top: 2vw;
  width: 92vw;
  height: 90vw;
}
.config-panel-vertical .fact-chart-preview div div {
  -webkit-transform-origin: center top;
          transform-origin: center top;
}
.config-panel-vertical .config-control {
  margin-top: 8px;
  padding: 2vw;
  padding-bottom: 15px;
}
.config-panel-vertical .config-control .caption .caption-img {
  top: calc(96vw + 50px);
}
.config-panel-vertical .config-control .config-control-detail {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.config-control-detail-capitalize {
  text-transform: capitalize;
}
