.recommendedFacts {
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: fixed;
  right: 5vw;
  top: 0vh;
  bottom: 0vh;
  margin: auto;
}
.recommendedFacts .ant-modal-content {
  box-shadow: 2px 2px 10px 0 rgba(138, 138, 138, 0.5);
  width: 390px;
  position: relative;
}
.recommendedFacts .ant-modal-content .ant-modal-close {
  top: -3px;
}
.recommendedFacts .ant-modal-content .ant-modal-header {
  background: #D7D4CD;
  height: 50px;
}
.recommendedFacts .ant-modal-content .ant-modal-header .ant-modal-title {
  color: #111;
  line-height: 14px;
}
.recommendedFacts .ant-modal-content .ant-modal-body {
  padding-left: 20px;
  padding-right: 20px;
  background-color: #f4f4f4;
}
.recommendedFacts .ant-modal-content .ant-modal-body .addNewFact {
  background-color: white;
  border-radius: 6px;
  padding: 12px;
  cursor: pointer;
  margin-bottom: 20px;
}
.recommendedFacts .ant-modal-content .ant-modal-body .addNewFact .addNewFactIcon {
  background: url(/static/media/add_gery.8edd811f.svg) no-repeat center;
  background-size: 18px;
}
.recommendedFacts .ant-modal-content .ant-modal-body .addNewFact .addNewFactText {
  color: #222;
  text-align: center;
}
.recommendedFacts .ant-modal-content .ant-modal-body .addNewFact:hover {
  box-shadow: 4px 4px 4px rgba(175, 164, 161, 0.3);
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList {
  max-height: 480px;
  overflow: scroll;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .recommendFact {
  display: flex;
  align-items: center;
  padding: 4px 10px;
  border-bottom: 1px solid #D7D4CD;
  min-height: 44px;
  cursor: default;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .recommendFact .recommendFactIcon {
  width: 22px;
  height: 40px;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .recommendFact .recommendFactIconextreme {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 20px 20px;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .recommendFact .recommendFactIconoutlier {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 20px 20px;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .recommendFact .recommendFactIconproportion {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 18px 18px;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .recommendFact .recommendFactIconrank {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 18px 18px;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .recommendFact .recommendFactDescription {
  margin-left: 12px;
  width: 300px;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .recommendFact:first-child {
  border-top: 1px solid #D7D4CD;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .recommendFact:hover {
  background-color: #DEDEDF;
}
.recommendedFacts .ant-modal-content .ant-modal-body .factList .noRecomendedFacts {
  text-align: center;
}
.annotationEditor {
  position: fixed;
  right: 5vw;
  top: calc(50vh - 668px/2) !important;
}
.annotationEditor .ant-modal-content {
  width: 390px !important;
}
